<template>
  <div id="BookManage">
    <div id="head">
      <ul>
        <li>
          <el-button type="primary" @click="adddata">添加<i class="el-icon-circle-plus"/></el-button>
        </li>
        <li>查询条件：</li>
        <li>
          <el-select v-model="value" placeholder="请选择">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li>
          <span> 搜索： </span>
        </li>
        <li @keyup.enter="searchBook">
          <el-input placeholder="请输入关键字" v-model:value="searchValue"></el-input>
        </li>
        <li>
          <el-button type="primary" @click="searchBook">查询</el-button>
        </li>
      </ul>
    </div>
    <div id="main">
      <el-empty description="请检索" v-show="false"></el-empty>
      <div id="sort">
        <ul>
          <li>员工编号</li>
          <li>员工姓名</li>
          <li>入职日期</li>
          <li>就职部门</li>
          <li>职位</li>
          <li>操作</li>
        </ul>
      </div>
      <div id="detail">
        <ul v-for="(item,index) in libs">
          <li><input :disabled="true" v-model:onfocusout="libs[index].sid"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].staffname"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].entryDate"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].department"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].job"></li>
          <li>
            <el-button id="amend" type="primary" v-show="editAble[index]" @click="amending(index)">
              修改
            </el-button>
            <el-button id="save" type="primary" v-show="showSaved[index]" @click="saving(index)">保存</el-button>
            <el-button id="delete" type="primary" @click="deleting(index)">删除</el-button>
          </li>
        </ul>
      </div>
    </div>
    <div id="bottom">
      <ul>
        <li>
          <el-button type="primary" @click="homepage">首页</el-button>
        </li>
        <li>
          <el-button @click="lastpage">上一页</el-button>
        </li>
        <li>第{{ nowPage }}/{{ totalPage }}页</li>
        <li>
          <el-button @click="nextpage">下一页</el-button>
        </li>
        <li>
          <el-button type="primary" @click="trailerpage">尾页</el-button>
        </li>
        <li>当前页共{{ this.libs.length }}条数据</li>
      </ul>
    </div>
  </div>
</template>

<!--随意的给数组赋值数组，在vue中赋值大多数时候都是引用其地址，会造成同一变量的结果，注意-->
<script>
import axios from "axios";

export default {
  name:"StaffManage",
  data() {
    return { //使用return包裹的变量只在当前组件中生效
      libs: [],
      libsSearch: [],//搜索数组
      searchValue: "",
      libsDatabase: [],//总数组
      transitDatabase: [],//中转数组
      nowPage: 1,
      totalPage: 1,
      allowed: false,
      showSaved: [false, false, false, false, false, false, false, false],
      editAble: [true, true, true, true, true, true, true, true],
      saveMode: 'amend',
      options: [
        {
          value: 'sid',
          label: '员工编号'
        }, {
          value: 'staffname',
          label: '员工姓名'
        }, {
          value: 'entryDate',
          label: '入职日期'
        }, {
          value: 'department',
          label: '就职部门'
        }, {
          value: 'job',
          label: '职位'
        },],
      value: 'staffname'
    }
  },
  methods: {

    searchBook() {
      if (this.searchValue !== "") {
        this.libsSearch = []; //清空搜索存储数组
        for (let i = 0; i < this.libsDatabase.length; i++) {
          switch (this.value) {
            case "sid":
              if (this.libsDatabase[i].sid === this.searchValue) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "staffname":  //调用函数，通过关键字查找匹配项
              if (this.strMatch(this.libsDatabase[i].staffname, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "entryDate"://调用函数，通过关键字查找匹配项
              if (this.strMatch(this.libsDatabase[i].entryDate, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "department":
              if (this.strMatch(this.libsDatabase[i].department, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "job":
              if (this.strMatch(this.libsDatabase[i].job, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
          }
        }
      }
      this.showData();
    },

    strMatch(str, key) {// 模糊匹配
      let newkey = key.split(/\s/g);//切割空格关键字
      for (let i = 0; i < newkey.length; i++) {
        if (str.match(newkey[i]) != null) {
          return true //match检索到目标字段，返回true
        }
      }
      return false
    },

    showData() {
      if (this.searchValue !== "") {
        this.libsDatabase = this.libsSearch;
        this.initalize();
      } else {
        this.linkDatabase();
      }
    },

    adddata() {//添加一排表格
      if (this.libs.length < 8) { //于当前页面有空余时，添加表格
        if (this.editAble[this.libs.length - 1] !== false) {
          //计算出下一位“Uxxxx”sid
          let sidNum = parseInt(this.libs[this.libs.length - 1].sid.slice(-4)) + 1; //获取sid中的含0纯数字部分
          let sidNumLen = sidNum.toString().length;//获取sid中的纯数字部分的长度
          let sidNew;//新的sid
          switch (sidNumLen) {
            case 1:
              sidNew = "S000" + sidNum.toString();
              break;
            case 2:
              sidNew = "S00" + sidNum.toString();
              break;
            case 3:
              sidNew = "S0" + sidNum.toString();
              break;
            case 4:
              sidNew = "S" + sidNum.toString();
              break;
          }
          this.libs.push({
            sid: sidNew,
            staffname: "",
            entryDate: "",
            department: "",
            job: "",
          });
          this.libsDatabase.push(this.libs[this.libs.length - 1]);
          this.saveMode = "insert";
          this.editAble[this.libs.length - 1] = false;
          this.showSaved[this.libs.length - 1] = true;
        }
      } else if (this.libs.length === 8) {//当前页面无空余时，添加表格
        if ((this.totalPage - this.nowPage) >= 1) { //当前页距离尾页超过一页
          this.trailerpage();//跳转尾页
          this.adddata();//回调自身,添加数据
        } else if ((this.totalPage - this.nowPage) === 0) { //当前页是尾页
          this.libs = [];//清空页面数组
          this.totalPage++;
          this.nowPage++;
          this.adddata();
        }
      }
    },
    // 修改（可编辑状态）
    amending(index) {
      this.editAble[index] = false;
      this.showSaved[index] = true;
      this.saveMode = "amend";
      this.$forceUpdate(); //解决修改属性数据后，页面不刷新的问题，通过强制刷新解决
    },
    // 保存（关闭可编辑状态）
    saving(index) {
      let that=this;
      if (this.libs[index].staffname === "" || this.libs[index].entryDate === "" || this.libs[index].department === "" || this.libs[index].job === "") {
        this.$message({
          message: '请输入相对应的值',
          type: 'warning'
        });
      } else {
        this.editAble[index] = true;
        this.showSaved[index] = false;
      /*  if (this.saveMode === "amend") {
          axios({    //修改数据
            method: 'post',
            url: 'http://localhost:3000/staffManage/amend',
            params: {
              sid: this.libs[index].sid,
              staffname: this.libs[index].staffname,
              entryDate: this.libs[index].entryDate,
              department: this.libs[index].department,
              job: this.libs[index].job,
            },
            data: {},
            //使用post一定要写，为何请求报文一定要规定的头，其原因不详
            //推测可能是浏览器或者服务端node的原因
            headers: {'content-Type': 'application/x-www-form-urlencoded'},
          }).then(function (response) {
            console.log(response)
          }).catch(function (error) {
            console.log(error);
          });
        } else {    //插入新数据
          axios({
            method: 'post',
            url: 'http://localhost:3000/staffManage/insert',
            params: {
              sid: this.libs[index].sid,
              staffname: this.libs[index].staffname,
              entryDate: this.libs[index].entryDate,
              department: this.libs[index].department,
              job: this.libs[index].job,
            },
            data: {},
            //使用post一定要写，为何请求报文一定要规定的头，其原因不详
            //推测可能是浏览器或者服务端node的原因
            headers: {'content-Type': 'application/x-www-form-urlencoded'},
          }).then(function (response) {
            console.log(response.data);
            if (response.data === "success!") {
              that.$message({
                message: '添加数据成功',
                type: 'success'
              });
            }
          }).catch(function (error) {
            console.log(error);
          });
        }*/
        this.$forceUpdate();
      }
    },
    // 删除
    deleting(index) {
      //删除数据库的数据
      /*let that=this;
      let deletedSid = this.libs[index].sid;
      axios({
        method: 'post',
        url: 'http://localhost:3000/staffManage/delete',
        params: {
          sid: this.libs[index].sid,
        },
        data: {},
        //使用post一定要写，为何请求报文一定要规定的头，其原因不详
        //推测可能是浏览器或者服务端node的原因
        headers: {'content-Type': 'application/x-www-form-urlencoded'},
      }).then(function (response) {
        console.log(response.data);
        if (response.data === "success!") {
          that.$message({
            message: "删除" + deletedSid + "号数据成功",
            type: 'success'
          });
        }
      }).catch(function (error) {
        console.log(error);
      });*/

      //index是从0开始的
      if (index === 0) {//是第一条数据时
        this.libs.shift();
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      } else if (index + 1 === this.libs.length) {//最后一条数据时
        this.libs.pop();
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      } else {//中间数据
        this.libs.splice(index, 1);
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      }
      if (this.libs.length === 0) {//没有数据时
        this.lastpage();
      }
    },
    // 上一页
    lastpage() {
      if (this.nowPage > 1) {//限制措施，防止第一页跳负页
        for (let i = 0; i < 8; i++) {
          this.libs[i] = this.libsDatabase[(this.nowPage - 2) * 8 + i];
        }
        this.nowPage--;
        this.$forceUpdate();//强制刷新页面
      }
    },
    // 下一页
    nextpage() {
      if (this.nowPage < this.totalPage) {//限制措施，防止尾页跳溢出页
        this.libs = [];//清空
        if ((this.totalPage - this.nowPage) > 1) { //满8条数据的下一页
          for (let i = 0; i < 8; i++) {
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];//把数据写入到当前页面数组
          }
        } else if (this.totalPage - this.nowPage === 1) {//不满8条数据的下一页
          for (let i = 0; i < this.libsDatabase.length % 8; i++) {
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];
          }
        }
        this.nowPage++;
        this.$forceUpdate();
      }
    },
    //首页
    homepage() {
      if (this.nowPage > 1) {
        for (let i = 0; i < 8; i++) {
          this.libs[i] = this.libsDatabase[i];
        }
        this.nowPage = 1;
        this.$forceUpdate();
      }
    },
    // 尾页
    trailerpage() {
      if (this.nowPage < this.totalPage) {
        this.libs = [];
        if ((this.libsDatabase.length % 8) !== 0) {//不满8条数据
          for (let i = 0; i < this.libsDatabase.length % 8; i++) {
            this.libs[i] = this.libsDatabase[(this.totalPage - 1) * 8 + i];
          }
        } else {
          for (let i = 0; i < 8; i++) {//满8条数据
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];
          }
        }
        this.nowPage = this.totalPage;
        this.$forceUpdate();
      }
    },
    initalize() {//初始话数据界面
      this.libs=[];
      if (this.libsDatabase.length >= 8) {
        for (let i = 0; i < 8; i++) {   //把第一面的数据写入
          this.libs.push(this.libsDatabase[i])
        }//此时在libdatabase中，所有的数据已经在了，所以只要更改页数就可以了
        this.totalPage = parseInt(this.libsDatabase.length / 8) + 1;
        this.nowPage = 1;
      } else {
        for (let i = 0; i < this.libsDatabase.length; i++) {   //把第一面的数据写入
          this.libs.push(this.libsDatabase[i])
        }
      }
    },
    // -----------------------------从数据库接入数据--------------------------
    linkDatabase() {
      this.libsDatabase=[
        {
          sid: "S0001",
          staffname: "于先生",
          entryDate: "2015年6月5日",
          department: "管理部门1",
          job: "图书馆馆长",
        },        {
          sid: "S0002",
          staffname: "小明",
          entryDate: "2019年5月1日",
          department: "图书部门2",
          job: "图书管理员",
        },        {
          sid: "S0003",
          staffname: "小方",
          entryDate: "2020年12月25日",
          department: "图书部门1",
          job: "图书管理员",
        },
      ]
/*      this.libsDatabase=[];
      axios.get('http://localhost:3000/staffManage/get').then((response) => {
        let that = this.libsDatabase;
        response.data.forEach(function (value, index) {
          that.push(response.data[index]);
        })
      })*/
      this.initalize();
    }
    // -----------------------------从数据库接入数据--------------------------
  },
  created() {
    this.linkDatabase();//连接数据库初始化
  }
}
</script>

<style scoped>
#BookManage {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}

#head {
  width: 100%;
  height: 10%;
  background-color: #f0f0f0;
}

#head ul {
  list-style: none;
  height: 100%;
  margin: 0;
  transform: scale(0.92);
}

#head ul li {
  float: left;
  margin-left: 5px;
  height: 100%;
  display: flex;
  align-items: center;
  font-size: 15px;
}

#head ul li:nth-child(2) {
  margin-left: 5%;
}

#head ul li:nth-child(3) {
  width: 120px;
}

#main {
  width: 100%;
  height: 80%;
}

/*顶部*/
#sort {
  margin-top: 1%;
  width: 100%;
  height: 30px;
  line-height: 30px;
}

#sort ul {
  width: auto;
  height: 100%;
  list-style: none;
  margin: 0 auto;
}

#sort ul li {
  width: 13%;
  height: 100%;
  float: left;
  padding-left: 10px;
  color: #4e4e4e;
  font-size: 13px;
  border: 1px solid #d8d8d8;
  background-color: #f0f0f0;
}
#sort ul li:nth-child(4){
  width: 20%;
}
#sort ul li:last-child {
  width: 17%;
}

/* 中部*/
#detail {
  width: 100%;
  height: 50px;
  line-height: 50px;
}

#detail ul {
  width: auto;
  height: 100%;
  list-style: none;
  margin: 0 auto;
}

#detail ul li {
  width: 13%;
  height: 100%;
  float: left;
  padding-left: 10px;
  color: #4e4e4e;
  font-size: 13px;
  border: 1px solid #d8d8d8;
}
#detail ul li:nth-child(4){
  width: 20%;
}
#detail ul li input {
  width: 100%;
  height: 95%;
  padding: 0px;
  font-size: 13px;
  border: none;
}

input[disabled] {
  background: #ffffff;
  color: black;
}

#detail ul li:last-child {
  text-align: center;
  width: 17%;
}

#amend {
  border: none;
}

#save {
  background-color: #57c746;
  border: none;
  margin-left: -1px;
}

#save:hover {
  background-color: #79e071;
}

#delete {
  border: none;
  background-color: #ff4040;
}

#delete:hover {
  background-color: #ff7979;
}

/*底部*/
#bottom {
  margin-top: 1%;
  width: 100%;
  height: 30px;
  line-height: 30px;
  transform: scale(.95);
}

#bottom ul {
  width: max-content;
  height: 100%;
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

#bottom ul li {
  width: 100px;
  height: 100%;
  float: left;
  color: #4e4e4e;
  text-align: center;
  font-size: 13px;
}

#bottom ul li:last-child {
  width: 120px;
}
</style>